@import "../../css/variables";
@import "../../css/mixins";

$boxSize: 415px;

.ratingBox{
    background-color: #212832;
    width:100%;
    max-width: $boxSize;

    padding: 2.4em;
    border-radius:2em;

    &--center{
        @include flex-align();
        flex-direction:column;
    }

    &__selection{
        @include flex-align();
        color:$orange;
        background-color:$mid-dark-blue;
        padding:.5em 1.5em;
        border-radius:1.5em;
        margin-top: 1.5em;
    }
}

.scoreButtons{
    display:flex;
    margin-top:2em;
    justify-content: space-between;
    width:98%;
    flex-wrap:wrap;
}

@media only screen and (max-width:300px){
    .scoreButtons{
        justify-content: center;
    }
}