@import "../../css/variables";
@import "../../css/mixins";


.circle{
    $size: 3.2em;
    @include circle($size);
    @include flex-align();
    background-color:$mid-dark-blue;
    color: $light-grey;
    transition: background-color 0.2s, color 0.2s;

    &__content{
        @include flex-align();
    }

    &--int{
        cursor:pointer;
        
        &:not(.circle--active):hover{
            background-color: $light-grey;
            color:white;
        }
    }

    &--active{
        background-color: $orange;
        color:white;
    }
}