@import "../../css/variables";
@import "../../css/mixins";

.button{
    width:100%;
    margin-top: 2.6em;
    background-color:$orange;
    border:none;
    border-radius:2em;
    color:white;
    height:45px;
    text-transform: uppercase;
    font-weight:bold;
    font-size: 105%;
    letter-spacing:.1rem;
    cursor:pointer;
    transition: background-color 0.2s, color 0.2s;

    &:hover{
        background-color:white;
        color: $orange;
    }
}