@import "../../css/variables";
@import "../../css/mixins";

*{
    margin:0;
    padding:0;
    box-sizing:border-box;
}

html{
    font-size: 93.75%;
    font-family: 'Overpass', sans-serif;
}

.main{
    @include flex-align();
    min-height:100vh;
    background-color: $very-dark-blue;
    padding:2em;
}

$size: 1440px;
$font-size:93.75%;

@while $size > 300px{
    $size: $size - 100px;
    $font-size: $font-size - 2.5%;


    @media only screen and (max-width:$size){
        html{
            font-size: $font-size;
        }
    }
}

